#header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  font-family: "Oswald", sans-serif;
  border-bottom: 1px solid #ddd;
  webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
  background-color: hsla(0, 0%, 69%, 0.1) !important;
  z-index: 9;

  #header-wr {
    max-width: 1280px;
    margin: 0 auto;

    nav {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: var(--header-height);
      text-transform: uppercase;

      #link {
        font-weight: 600;
        font-size: 1.5rem;
        letter-spacing: -0.04em;
      }

      ul {
        display: flex;

        li {
          font-weight: 400;

          &:not(:first-child) {
            margin-left: 1.25em;
          }
        }
      }
    }
  }
}

#ft {
  background-color: #fff;
  padding: 2em 0;
  font-family: 'Noto Sans KR', sans-serif;

  .ft-txt {
    text-align: center;
  }

  .ft-contact {
    text-align: center;
    margin-top: 1em;
    h2 {
      font-size: 2em;
      font-weight: 600;
      text-transform: uppercase;
    }

    .ft-contact-wr {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 0 0.5rem;
      margin-top: 0.25rem;

      font-size: 1.5em;
    }
  }
}

@media all and (max-width:1280px) {
  #header {
    #header-wr {
      padding: 0 1em;
    }
  }
}