#main {
  padding-top: var(--header-height);

  font-family: 'Noto Sans KR', sans-serif;

  .container {
    max-width: 1280px;
    margin: 0 auto;
    padding-top: 4em;
  }

  .container:not(#intro .container) {
    padding-bottom: 4em;
  }

  .flex-box {
    display: flex;
  }

  .sc-tit {
    font-family: "Oswald", sans-serif;
    font-size: 4em;
    font-weight: 600;
    letter-spacing: -0.02em;
    text-align: center;
    text-transform: uppercase;

    margin-bottom: 2em;
  }

  &>div:nth-child(even) {
    background-color: #fff;
  }

  #intro {
    .container {
      padding-top: 0;
    }

    .txt-box {
      justify-content: center;
      align-items: center;
      gap: 0 1.5em;
      height: calc(100vh - var(--header-height));

      h2 {
        font-size: 2em;
        font-weight: 600;
        line-height: 1.3;
      }

      p {
        color: #666;
        font-weight: 300;
        letter-spacing: -0.02em;

        &:last-child {
          margin-top: 1.5em;
        }
      }
    }
  }

  #skills {
    .skills-wr {
      justify-content: center;
      align-items: flex-start;
      gap: 0 4em;

      .skills-inner {
        justify-content: center;
        align-items: flex-start;
        gap: 0 2em;
        text-align: center;

        &>div {
          width: 140px;
        }

        .skills-tit {
          justify-content: center;
          align-items: center;
          gap: 0 0.25em;
          font-size: 2em;
          font-weight: 600;
          letter-spacing: -0.02em;
          text-transform: uppercase;
          text-align: right;
          padding: 0 0.25em;
        }

        .skills-txt-wr {
          margin-top: 1em;

          p {
            font-size: 1.25em;
            line-height: 1.7;
          }
        }
      }
    }

    .skills-txt {
      font-weight: 300;
      color: #666;
      letter-spacing: -0.02em;
    }
  }

  #works {
    .works-wr {
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-start;
      flex-wrap: wrap;
      gap: 3em 2em;

      &>li {
        width: calc(50% - 1em);
        background-color: #fff;
        padding: 2em;
        border-radius: 20px;
        overflow: hidden;
      }

      .works-link {
        display: block;
        transition: all .35s ease-in-out;
        position: relative;

        .works-img {
          width: 100%;
          height: 300px;
          border-radius: 20px;
          background-color: red;
          background-size: cover;
          background-repeat: no-repeat;
          background-position: center;
        }

        .works-txt {
          margin-top: 1em;

          .works-title {
            font-size: 1.5em;
            font-weight: 600;
            letter-spacing: -0.02em;
          }

          .works-date {
            // font-weight: 400;
            color: #666;
            letter-spacing: -0.02em;
          }
        }

        .works-use-skills {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          gap: 0 0.25em;

          .skills-img {
            width: 2.5em;
            height: 2.5em;
            object-fit: cover;
          }
        }
      }
    }
  }
}

@media all and (max-width:1280px) {
  #works {
    .container {
      padding: 4em 1em;
    }
  }
}

@media all and (max-width:768px) {
  #main {

    .sc-tit {
      margin-bottom: 1em;
      font-size: 3.5em;
    }

    #intro {
      .txt-box {
        flex-direction: column;
        text-align: center;

        div {
          margin-top: 1em;
        }
      }
    }

    #skills {

      .skills-wr {
        flex-direction: column;
        align-items: center;

        .skills-inner {
          width: calc(100% - 2em);
          justify-content: space-between;
        }

        .skills-txt {
          margin-top: 2em;
        }
      }
    }

    #works {
      .works-wr {
        gap: 2em 0;
        flex-direction: column;

        &>li {
          width: 100%;
        }

        .works-link {
          .works-img {
            height: 200px;
          }
        }
      }
    }
  }


}