#work-detail {
  padding-top: var(--header-height);
  background-color: #fff;
  height: 100vh;
  .container {
    max-width: 1280px;
    margin: 0 auto;
    padding: 4em 0;
    font-family: 'Noto Sans KR', sans-serif;

    .work-detail-wr {
      display: flex;
      justify-content: center;
      align-items: flex-start;
      gap: 0 2.5%;

      .work-detail-img {
        width: 50%;
        border-radius: 20px;
        overflow: hidden;
      }

      .work-detail-info {
        width: 47.5%;
      }

      .work-tit {
        font-size: 2em;
        font-weight: 600;
        letter-spacing: -0.02em;
      }

      .work-date {
        margin-top: 0.5em;
        font-size: 1.25em;
        font-weight: 300;
        color: #666;
        letter-spacing: -0.02em;
      }

      .work-sub {
        margin-top: 0.5em;
        font-size: 1.25em;
        font-weight: 600;
        color: #666;
      }

      .work-des {
        margin-top: 0.5em;
        letter-spacing: -0.02em;
        line-height: 1.6;
        word-break: keep-all;
      }

      .work-detail-use {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 1em;

        h2 {
          font-size: 2em;
          font-weight: 600;
          letter-spacing: -0.02em;
        }

        .work-skill-img-wr {
          display: flex;
          justify-content: flex-end;
          align-items: center;
          gap: 0 0.25em;
        }
        
        .skills-img {
          width: 2.5em;
          height: 2.5em;
          object-fit: cover;
        }
      }

      .work-detail-link {
        text-align: center;

        a {
          margin-top: 1em;
          padding: 0.75em 0;
          display: block;
          width: 100%;
          background-color: #666;
          font-size: 1.25em;
          color: #fff;
          border-radius: 5px;
        }
      }
    }
  }
}

@media all and (max-width:1280px) {
  #work-detail {
    height: auto;
    background-color: #f4f4f4;
    .container {
      padding: 4em 1em;
      .work-detail-wr {
        flex-direction: column;
        gap: 2em 0;

        .work-detail-img {
          width: 100%;
          height: 400px;
          object-fit: cover;
          object-position: center;

          img {
            height: 100%;
          }
        }

        .work-detail-info {
          width: 100%;
        }
      }
    }
  }
}

@media all and (max-width:768px) {
  #work-detail {
    .container {
      .work-detail-wr {
        .work-detail-img {
          height: auto;
        }
      }
    }
  }
}